<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <img src="../assets/heima.png" alt="logo" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <!-- 页面主体 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div @click="toggleCollapse" class="toggle-button">|||</div>
        <Aside :isCollapse="isCollapse" />
      </el-aside>
      <!-- 右侧内容主题 -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Aside from '../components/HomeAside.vue'
export default {
  name: 'Home',
  data() {
    return {
      //  是否折叠侧边栏
      isCollapse: false,
    }
  },
  components: { Aside },
  methods: {
    logout() {
      this.$message.success('正在退出')
      setTimeout(() => {
        window.sessionStorage.clear()
        this.$router.push('/login')
        this.$message.success('退出成功')
      }, 1500)
    },
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
  },
}
</script>

